:root {
	--bgcolor: #ffffff;
	--txtcolor: #152035;
	--txtcolorh: #f67280;
}

*{
	transition: all .5s ease-in-out;
}

html {
	scroll-behavior: smooth;
	overflow-x: hidden;
	direction: rtl;
}

img{
	width: 100%;
}

body {
	font-family: IranSansDN, serif;
	text-align: right;
	font-weight: normal;
	background-color: #F9F9FF;
	font-size: 14px;
	position: relative;
	color: var(--txtcolor);
	overflow-x: hidden;
}

.container {
	&--1500 {
		@media screen and (min-width: 1500px) {
			width: 1500px;
		}
	}
}

a:hover{
	color: var(--txtcolorh);
}
