.category-main,.page-main{
	padding-top: 100px;
    
	&__top{
		&-breadcrumb{
			color: #506172;
		}

		&-subject{
			column-gap: 10px;
			h1{
				font-weight: bold;
				font-size: 1.8rem !important;
				&.page-title{
					color: var(--txtcolorh);
				}
			}
		}
	}
	&__content{
		margin: 30px 0;
		&-posts{
			column-gap: 20px;
			justify-content: flex-start;
			align-items: flex-start;
			.post-card{
				max-width: 48%;
			}
		}
	}
}