.single-main {
	&__content {
		flex-grow: 1;
    
	}
  
	.post-card {
		max-width: unset;
		width: 100%;
    
		&__content {
			ul {
				list-style: disc;
        
				li {
					& > ul {
						list-style: circle;
						padding-right: 30px;
					}
				}
			}
      
			ol {
				list-style: decimal;
        
				li {
					& > ol {
						padding-right: 30px;
					}
				}
			}
		}
    
		&__information {
			display: flex;
			align-items: center;
			justify-content: flex-start;
			column-gap: 25px;
			margin: 10px 0 35px;
      
			i {
				color: var(--txtcolorh);
				font-size: .4rem;
			}
      
			&-author {
				display: flex;
				column-gap: 10px;
				align-items: center;
        
				img {
					border-radius: 50%;
				}
			}
		}
    
		&__footer {
			margin-top: 30px;
      
			&-tags {
				ul {
					width: 100%;
					display: flex;
					align-items: center;
					justify-content: flex-start;
					column-gap: 5px;
					flex-wrap: wrap;
          
					li {
						background-color: #F9F9FF;
						padding: 4px 11px;
						border-radius: 20px;
						color: #506172;
					}
				}
			}
		}
	}
  
	.navigation-post {
		column-gap: 30px;
		&__prev, &__next {
			background-color: #ffffff;
			padding: 30px;
			border-radius: 20px;
			box-shadow: 0px 5px 20px 0px rgba(69, 67, 96, 0.1);

			.content{
				column-gap: 15px;

				.img{
					width: 35%;
					max-width: 85px;
					border-radius: 20px;
					overflow: hidden;
					cursor: pointer;

					img{
						&:hover{
							transform: scale(1.1);
						}
					}
				}
              
                .text{
					display: flex;
					flex-direction: column;
					row-gap: 10px;
                  &-top{
                    display: flex;
                    align-items: center;
                    justify-content: flex-start;
					  a{
						  display: flex;
						  align-items: center;
						  justify-content: center;
						  column-gap: 10px;
						  background-color: var(--txtcolorh);
						  color: white;
						  font-weight: bold;
						  padding: 3px 10px;
						  border-radius: 30px;
						  font-size: .75rem;
						  &:hover{
							  background-color: var(--txtcolor);
							  color: white;
						  }
					  }
                  }
					&-bottom{
						font-weight: bold;
					}
                }
			}
		}
	}
	
	&__sidebar-author{
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		background-color: #ffffff;
		padding: 30px;
		border-radius: 20px;
		margin-bottom: 30px;
		box-shadow: 0px 5px 20px 0px rgba(69, 67, 96, 0.1);
		img{
			max-width: 96px;
			border-radius: 50%;
			margin-bottom: 15px;
		}
		h3{
			font-weight: bold;
			font-size: 1.6rem;
			margin-bottom: 7px;
		}
		p{
			text-align: justify;
			text-align-last: center;
		}
	}
}


.spacer {
	display: block;
	margin-top: 150px;
}