.notfound-content{
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100vh;

	.notfound-container{
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		background-color: #ffffff;
		padding: 50px;
		border-radius: 20px;
		box-shadow: 0 5px 20px 0 rgba(69, 67, 96, 0.1);
		cursor: pointer;

		span{
			color: var(--txtcolorh);
			font-weight: bold;
			font-size: 10rem;
			line-height: 1;
		}

		h4{
			font-weight: bold;
		}

		a{
			background-color: var(--txtcolorh);
			border-radius: 30px;
			padding: 3px 13px;
			color: white;
			margin-top: 20px;

			&:hover{
				background-color: var(--txtcolor);
			}
		}
	}
}