@media screen and (max-width: 991px) {
	.category-main{
		.category-main__top{
			margin-top: 80px;
		}

		.main-content{
			.category-main__content{
				&-posts{
					.post-card{
						max-width: 100%;
					}
				}
			}
		}
	}
}

@media screen and (max-width: 767px) {
	.category-main{
		.category-main__top{
			margin-top: 80px;
		}
		.main-content{
			flex-direction: column;
			.category-main__sidebar{
				margin-right: 0 !important;
			}
		}
	}
}