.swiper {
	width: 100%;
	height: 100vh;
  
	&-button-prev, &-button-next {
		position: absolute;
		top: 50%;
		z-index: 10000;
		color: white;
		font-size: 2rem;
	}
  
	&-button-prev {
		right: 40px;
	}
  
	&-button-next {
		left: 40px;
	}
  
	&-pagination {
		display: flex;
		justify-content: center;
		align-items: center;
		height: 20px;
		width: 100%;
		position: absolute;
		bottom: 15px;
		z-index: 100000;
		column-gap: 5px;
    
		&-bullet {
			width: 13px;
			height: 10px;
			background-color: whitesmoke;
			border-radius: 30px;
      
			&-active {
				background-color: var(--txtcolorh);
				width: 22px;
			}
		}
	}
}

.swiper-slide {
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	background: #000;
  
	& > img {
		width: 100%;
		flex-grow: 1;
	}
  
	& > .slide-content {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 100%;
		height: 100%;
		background-color: rgba(0, 0, 0, .3);
    
		.slide-content__categories {
			display: flex;
			justify-content: center;
			align-items: center;
			column-gap: 9px;
      
			& > .slider-cat {
				background-color: var(--txtcolorh);
				color: white;
				border-radius: 100px;
				padding: 3px 10px;
        
				h4 {
					font-size: .9rem;
					font-weight: bold;
				}
        
				&:hover {
					background-color: #2b2b2b;
				}
			}
		}
    
		.slide-content__title {
			margin: 50px 220px;
      
			h1 {
				text-align: center;
				color: white;
				font-weight: bold;
			}
		}
    
		.slide-content__more {
			display: flex;
			align-items: center;
			justify-content: center;
			color: white;
			font-weight: bold;
			font-size: .8rem;
      
			&-right {
				display: flex;
				align-items: center;
				justify-content: center;
				column-gap: 5px;
			}
      
			&-center {
				display: flex;
				align-items: center;
				justify-content: center;
				column-gap: 5px;
				padding: 0 15px;
				margin: 0 15px;
				border-left: 2px solid var(--txtcolorh);
				border-right: 2px solid var(--txtcolorh);
			}
      
			&-left {
				display: flex;
				align-items: center;
				justify-content: center;
				column-gap: 5px;
        
				img {
					border-radius: 50%;
					max-width: 35px;
				}
			}
		}
	}}

.posts-container {
	margin: 100px 0;
  
	& > .row {
		column-gap: 20px;
		justify-content: center;
		align-items: flex-start;
	}
}

.post-card {
	background-color: #ffffff !important;
	padding: 30px;
	border-radius: 20px !important;
	margin-bottom: 60px;
	display: block;
	position: relative;
	box-shadow: 0 5px 20px 0 rgba(69, 67, 96, 0.1);
	max-width: 32%;
  
	&__img {
		margin-top: -60px;
		margin-bottom: 30px;
		position: relative;
		border-radius: 20px;
		overflow: hidden;
		box-shadow: 0 10px 10px rgba(0, 0, 0, 0.08), 0 0 0 transparent;
    
		img {
			border-radius: 20px;
			transition: all 0.9s ease-in-out;
      
			&:hover {
				transform: scale(1.1);
			}
		}
	}
  
	&__content {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: flex-start;
    
		&-categories {
			display: flex;
			justify-content: center;
			align-items: center;
			column-gap: 5px;
			margin-bottom: 20px;
      
			& > .post-cat {
				background-color: var(--txtcolorh);
				color: white;
				border-radius: 100px;
				padding: 3px 10px;
        
				h4 {
					font-size: .8rem;
					font-weight: bold;
				}
        
				&:hover {
					background-color: #2b2b2b;
				}
			}
		}
    
		&-title {
			font-weight: bold;
			font-size: 1.09rem;
			line-height: 1.6;
			margin-bottom: 8px;
      
			&:hover {
				a {
					color: var(--txtcolorh);
				}
			}
		}
    
		&-desc {
			margin-bottom: 30px;
			color: #506172;
			text-align: justify;
		}
	}
  
	&__footer {
		display: flex;
		justify-content: flex-start;
		align-items: center;
    
		&-author {
			img {
				max-width: 40px;
				border-radius: 50%;
				margin-left: 7px;
			}
      
			font-weight: normal;
		}
    
		i {
			font-size: .3rem;
			color: var(--txtcolorh);
			margin: 0 15px;
		}
	}
}

.pagination {
	display: flex;
	justify-content: center;
	align-items: center;
	column-gap: 10px;
  
	& > .page-numbers {
		padding: 10px 13px;
		border: 1px solid var(--txtcolorh);
		border-radius: 50px;
		color: var(--txtcolorh);
		font-weight: bold;
		line-height: 1;
	}
  
	.current {
		color: white;
		background-color: var(--txtcolorh);
	}
  
	& > a.page-numbers {
		&:hover {
			color: white;
			background-color: var(--txtcolorh);
		}
	}
}

.box-border-slider{
	position: relative;
	
	.swiper{
		&-slide{
			border-radius: 20px;
			overflow: hidden;
			opacity: .5;
			transition: all .2s ease-in-out;

			&-active{
				opacity: 1;
				box-shadow: 0 5px 20px 0 rgba(69, 67, 96, 0.1);
			}
		}
		&-button-prev{
			right: 15px;
		}
		&-button-next{
			left: 15px;
		}
		&-button-prev,&-button-next{
			position: absolute;
			display: flex;
			align-items: center;
			padding: 25px;
			border-radius: 18px;
			background-color:rgba(255,255,255,.45);
			height: calc(100% - 30px);
			top: 15px;
		}
	}
}