footer#footer {
	display: flex;
	align-items: center;
	justify-content: space-between;
	flex-direction: column;
	background-color: white;
  
	.subscribe {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		padding: 60px;
    
		&__title {
			font-weight: bold;
		}
    
		.tnp-subscription {
			max-width: 100%;
			margin: 50px 0 30px;
      
			form {
				display: flex;
				align-items: center;
				justify-content: center;
				border-radius: 50px;
				border: 1px solid #eee;
				overflow: hidden;
        
				.tnp-field {
					display: flex;
					flex-direction: column;
					align-items: flex-start;
					justify-content: center;
					flex-grow: 1;
					margin: 0;
          
					&-email {
						label {
							display: none;
						}

						input {
							width: 100%;
							padding: 0 25px;
							font-weight: bold;
						}
					}
					
					&-button{
						input{
							width: 100%;
							margin: 0;
							padding: 14px 30px;
							font-weight: bold;
							color: white;
							background-color: var(--txtcolorh);
						}
					}
				}
			}
		}
		
		//
		//	.tnp-field-button>input{

		//	}
		//}
    
		&__socials {
			display: flex;
			justify-content: center;
			align-items: center;
			column-gap: 35px;
      
			& > a {
				display: flex;
				align-items: center;
				justify-content: center;
				color: #506172;
        
				&:hover {
					color: var(--txtcolorh);
				}
        
				& > i {
					font-size: 1.1rem;
					margin-right: 5px;
				}
			}
		}
	}
  
	.footer__bottom {
		width: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		border-top: 1px solid #eee;
		padding: 20px 0;
    
		&-copyright {
			flex-grow: 1;
			text-align: center;
      
			a {
				color: var(--txtcolorh);
			}
		}
    
		&-devlink {
			margin-top: 10px;
			opacity: .5;
      
			&:hover {
				opacity: 1;
        
				a {
					color: var(--txtcolor);
				}
        
			}
		}
	}
}