ul#main-sidebar {
	& > li {
		background-color: #ffffff;
		padding: 40px 30px 30px;
		border-radius: 20px;
		margin-bottom: 30px;
		box-shadow: 0px 5px 20px 0px rgba(69, 67, 96, 0.1);
  
		.wp-block-group {
			&__inner-container {
				h2, h1, h3, h4, h5, h6 {
					position: relative;
					font-weight: bold;
					font-size: 1.2rem;
          
					&:before {
						content: '';
						background-color: var(--txtcolorh);
						position: absolute;
						height: 3px;
						width: 40px;
						top: -10px;
					}
				}
        
				ul.wp-block-latest-posts {
					margin-top: 40px;
          
					&__list {
						li {
							display: grid;
							grid-template-columns: 1fr 1fr 1fr;
							grid-template-rows: 1fr 1fr 1fr;
							gap: 0px 0px;
							grid-template-areas: "img title title"
							"img date date"
							". . .";
              
							.wp-block-latest-posts__featured-image {
								grid-area: img;
							}
              
							a {
								grid-area: title;
							}
              
							time {
								grid-area: date;
								display: flex;
								align-items: center;
							}
              
              
							.wp-block-latest-posts__featured-image {
								max-width: 85px;
								border-radius: 20px;
								overflow: hidden;
                
								&:hover {
									& > img {
										transform: scale(1.1);
									}
								}
							}
						}
					}
				}
        
				ul.wp-block-categories {
					margin-top: 30px;
					display: flex;
					flex-direction: column;
					align-items: stretch;
					justify-content: center;
					row-gap: 15px;
          
					li {
						display: flex;
						align-items: center;
						justify-content: space-between;
						background-color: #F9F9FF;
						padding: 15px 20px;
						border-radius: 10px;
						font-weight: bold;
            
						a {
							background-color: #f67280;
							color: #ffffff;
							padding: 2px 15px;
							border-radius: 20px;
						}
					}
				}
    
				.wp-block-tag-cloud{
					display: flex;
					flex-wrap: wrap;
					align-items: center;
					justify-content: flex-start;
					column-gap: 5px;
					row-gap: 5px;
					margin-top: 30px;

					a{
						font-size: .9rem !important;
						font-weight: bold;
						background-color: #F9F9FF;
						padding: 4px 11px;
						border-radius: 20px;
						color: #506172;
                      &:hover{
                        background-color: var(--txtcolorh);
                        color: white;
                      }
					}
				}
			}
		}
	}
}