@font-face {
    font-family: IranSansDN;
    font-style: normal;
    font-weight: normal;
    src: url("./fonts/iransansdn/iransansdnweb.eot");
    src: url("./fonts/iransansdn/iransansdnweb.eot")
        format("embedded-opentype"),
      /* IE6-8 */ url("./fonts/iransansdn/iransansdnweb.woff")
        format("woff"),
      /* FF3.6+, IE9, Chrome6+, Saf5.1+*/
        url("./fonts/iransansdn/iransansdnweb.ttf") format("truetype");
}

