.comment-form{
	background-color: #ffffff;
	padding: 30px;
	border-radius: 20px;
	margin: 30px 0;
	box-shadow: 0px 5px 20px 0px rgba(69, 67, 96, 0.1);
  
	.form__title{
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 20px;

		&-right{
			font-size: 1.2rem;
			font-weight: bold;
		}

		a{
			background-color: var(--txtcolorh);
			color: white;
			border-radius: 30px;
			padding: 3px 10px;

			&:hover{
				background-color: var(--txtcolor);
			}
		}
	}

	.form__textarea{
		textarea{
			resize: none;
			width: 100%;
			background-color: #F9F9FF;
			border: 1px solid #eee;
			border-radius: 20px;
			padding: 22px 20px;
			color: #152035;
			cursor: pointer;

			&:hover{
				box-shadow: 0px 5px 20px 0px rgba(69, 67, 96, 0.1);
				background-color: #ffffff;
			}
		}
	}

	.form__footer{
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		justify-content: flex-start;

		.form__inputs{
			display: flex;
			justify-content: space-between;
			align-items: center;
			flex-grow: 1;
			width: 100%;
			column-gap: 10px;
			margin-top: 15px;

			.form__input{
				flex-grow: 1;

				input{
					width: 100%;
					resize: none;
					width: 100%;
					background-color: #F9F9FF;
					border: 1px solid #eee;
					border-radius: 20px;
					padding: 15px 20px;
					color: #152035;
					cursor: pointer;

					&:hover{
						box-shadow: 0px 5px 20px 0px rgba(69, 67, 96, 0.1);
						background-color: #ffffff;
					}
				}
			}
		}

		.form__button{
			margin-top: 15px;
			width: 100%;

			button{
				background-color: var(--txtcolorh);
				color: white;
				padding: 10px 20px;
				border-radius: 100px;

				&:hover{
					background-color: var(--txtcolor);
				}
			}
		}
	}
}

.comment-sec{
	width: 100%;
	background-color: #ffffff;
	padding: 30px;
	border-radius: 20px;
	margin-top: 30px;
	box-shadow: 0px 5px 20px 0px rgba(69, 67, 96, 0.1);

	.comment-sec__sec-ttl{
		display: flex;
		align-items: center;
		justify-content: space-between;

		h5{
			font-weight: bold;
      
		}
	}

	.comment__lists{
		margin-top: 20px;

		ul{
			li{
				.comment{
					display: flex;
					align-items: flex-start;
					justify-content: flex-start;
					column-gap: 20px;
					margin-bottom: 10px;

					img{
						border-radius: 50%;
						max-width: 60px;
					}

					.comment__info{
						flex-grow: 1;

						.comment__title{
							display: flex;
							align-items: center;
							justify-content: flex-start;
							column-gap: 20px;

							span{
								font-weight: bold;
							}

							i{
								color: var(--txtcolorh);
								font-size: .4rem;
							}
						}

						p{
							margin: 15px 0;
						}

						.comment__footer{
							display: flex;
							align-items: center;
							justify-content: flex-end;

							&-left{
								.comment-reply-link{
									background-color: var(--txtcolorh);
									color: white;
									border-radius: 50px;
									padding: 3px 10px;

									&:hover{
										background-color: var(--txtcolor);
									}
								}
							}
						}
					}
				}

				&>ul.children{
					margin-right: 50px;
				}
			}
		}
	}
}