header#full-width-header {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 15px;
	background-color: white;
	position: fixed;
	top: 0;
	right: 0;
	left: 0;
	z-index: 1030;
	flex-direction: column;
	
	.container{
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	
  
	.header__center {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		flex-grow: 1;
		margin: 0 70px;
    
		.container_header_menu {
			display: flex;
			align-items: center;
			justify-content: flex-start;
      
			.header_menu {
				display: flex;
				align-items: center;
				justify-content: flex-start;
				column-gap: 30px;
        
				.menu-item {
					font-weight: bold;
          
					a {
						color: var(--txtcolor);
					}
          
					&:hover {
						a {
							color: var(--txtcolorh);
						}
					}
          
					&.menu-item-has-children {
						& > a {
							&:after {
								font-family: 'Font Awesome 6 Duotone';
								content: "\f078\f078";
								margin-right: 10px;
							}
						}
            
						ul.sub-menu {
							display: none;
							position: absolute;
							background-color: #f1f1f1;
							min-width: 160px;
							box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
							z-index: 1;
							padding: 20px;
							border-radius: 10px;
              
							&.active {
								display: flex;
								flex-direction: column;
								row-gap: 7px;
							}
              
							& > li.menu-item {
								display: flex;
								align-items: center;
                
								&:before {
									font-family: 'Font Awesome 6 Duotone';
									content: "\f111\10f111";
									margin-right: 10px;
									font-size: .5rem;
								}
                
								&:hover {
									& > a {
										color: var(--txtcolorh);
									}
								}
                
								& > a {
									color: var(--txtcolor);
								}
							}
						}
					}
				}
			}
		}
	}
    .header__left{
		display: flex;
		align-items: center;
		justify-content: flex-end;
		column-gap: 30px;
		&-social{
			display: flex;
			justify-content: flex-end;
			align-items: center;
			column-gap: 10px;
			&>a{
				font-size: 1rem;
				color: var(--txtcolor);
				&:hover{
					color: var(--txtcolorh);
				}
			}
		}
		&-search{
			display: flex;
			justify-content: center;
			align-items: center;
			span{
				display: flex;
				justify-content: center;
				align-items: center;
				width: 35px;
				height: 35px;
				background-color: var(--txtcolorh);
				color: white;
				border-radius: 50%;
				cursor: pointer;
				border: 2px solid transparent;
				&:hover{
					border: 2px solid var(--txtcolorh);
					background-color: white;
					color: var(--txtcolorh);
				}
			}
		}
	}
	.search-form{
		display: none;
	}
	.search-form.active{
		display: flex;
		align-items: center;
		justify-content: center;
		margin-top: 10px;
		padding-top: 10px;
		border-top: 1px solid var(--txtcolorh);
		form{
			display: flex;
			align-items: center;
			justify-content: space-between;
			width: 60%;
			border-radius: 30px;
			overflow: hidden;
			font-size: 1rem;
			border: 1px solid #eee;
			input,button{
				padding: 7px 20px;
			}
			button{
				background-color: var(--txtcolorh);
				color: white;
				&:hover{
					background-color: var(--txtcolor);
				}
				padding: 8px 20px;
			}
			input{
				overflow: hidden;
				flex-grow: 1;
			}
		}
	}
}

//menu-responsive
.mega-menu-site-responsive {
	position: fixed;
	transition: 0.5s ease-in-out;
	right: -100% !important;
	display: flex;
	flex-direction: column;
	top: 0;
	z-index: 9999999999999999999;
	width: 100%;
	overflow: scroll;
	bottom: 0;
	
	&__user {
		width: 35px;
		height: 35px;
		border-radius: 50%;
		display: inline-block;
		background-color: #054fa3;
		text-align: center;
		line-height: 37px;
		margin-right: auto;
		
		i {
			font-size: 15px;
			font-weight: 700;
			color: #ffffff;
			
		}
		
	}
	
	&__header {
		display: flex;
		align-items: center;
		padding: 18px 15px 0;
		justify-content: space-between;
		margin-bottom: 25px;
		
		.close {
			font-size: 22px;
			transition: .3s;
			cursor: pointer;
			color: white;
			background-color: var(--txtcolorh);
			padding: 2px;
			display: flex;
			border-radius: 50%;
		}
		
	}
	
	&__head {
		margin-bottom: 1px;
		background-color: rgba(255, 255, 255, .97);
		flex: 0 1 auto;
		max-height: 213px;
		
		form {
			display: flex;
			align-items: center;
			position: relative;
			flex-direction: row-reverse;
			padding: 0 15px;
			
			&:after {
				content: "";
				position: absolute;
				background-color: #e8edf0;
				width: 1px;
				top: -3px;
				bottom: -3px;
				left: 0;
			}
			
			i {
				font-size: 15px;
				color: #054fa3;
			}
			
			input {
				width: 100%;
				background-color: transparent;
				
				&::placeholder {
					color: #b5babe;
					font-size: 12px;
					font-weight: 500;
					opacity: 1;
				}
			}
			
			button {
				background-color: #fff;
				margin-right: 27px;
				padding: 0;
				
				i {
					font-size: 13px;
					color: #3b444a;
				}
			}
		}
	}
	
	&__icon-log {
		display: flex;
		align-items: center;
		width: 100%;
	}
	
	&__body {
		background-color: rgba(255, 255, 255, .97);
		margin-bottom: 1px;
		overflow: auto;
		flex-grow: 1;
		
		ul {
			list-style: none;
			text-align: right;
			margin-bottom: 0;
			padding: 0 15px 17px;
			
			> li {
				position: relative;
				
				a {
					cursor: pointer;
					display: flex;
					align-items: center;
					color: #0d1834;
					font-size: 13px;
					font-weight: 400;
					padding: 9px 0;
					line-height: 16px;
					
					i {
						color: #1049dc;
						font-size: 17px;
						transition: .3s;
						margin-left: 15px;
						position: relative;
						bottom: -2px;
					}
				}
				
				&.has-children {
					padding-right: 14px;
					
					&.active {
						a {
							&:after {
								display: none;
							}
						}
					}
					
					> a {
						position: relative;
						
						&:before {
							content: "";
							position: absolute;
							background-color: #6c767c;
							width: 9px;
							height: 1px;
							right: -16px;
							bottom: 16px;
						}
						
						&:after {
							content: "";
							position: absolute;
							background-color: #6c767c;
							width: 1px;
							height: 7px;
							right: -12px;
							bottom: 13px;
							
						}
					}
					
					> ul {
						display: none;
					}
				}
				
			}
		}
	}
	
	&__footer {
		background-color: rgba(255, 255, 255, .97);
		padding: 30px 35px;
		max-height: 80px;
		
		ul {
			text-align: center;
			display: block;
			
			li {
				display: inline-block;
				margin-left: 10px;
				
				a {
					
					i {
						font-size: 19px;
						color: #414b59;
					}
				}
			}
		}
	}
	
	&.active {
		right: 0 !important;
	}
	
	.logo-response {
		display: flex;
		justify-content: center;
		margin-bottom: 34px;
		
		i {
			font-size: 45px;
			color: #333333;
		}
	}
	
	&__close {
		position: absolute;
		left: 10px;
		top: 10px;
		cursor: pointer;
		i {
			transition: .2s ease-in-out;
			font-size: 14px;
		}
		
		&:hover {
			i {
				color: #d9132e;
			}
		}
	}
}

.mega-menu-responsive {
	display: none;
	cursor: pointer;
	margin-left: 10px;
	background-color: var(--txtcolorh);
	color: white;
	padding: 5px 7px 0;
	border-radius: 50%;
	
	i {
		font-size: 17px;
	}
}

.mask-shadow {
	position: fixed;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.2);
	transition: 0.5s ease-in-out;
	visibility: hidden;
	top: 0;
	opacity: 0;
	z-index: 9;
	right: 0;
	
	
	&.active {
		opacity: 1;
		visibility: visible;
	}
}

//end-menu-responsive